import React, { Suspense } from 'react';
import 'rsuite/lib/styles/index.less';
import './App.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Header, Login, Register, Feed, Invite, FooterTab, HeaderTab } from "./components"
import { Router, Route, Switch, useLocation, Redirect } from "react-router-dom";
import history from "./helpers/History"
import { isMobile } from "react-device-detect"

const App = () => {
  // let location=useLocation()
  const pages = [
    {
      pageLink: '/',
      view: Header,
      displayName: 'Navbar',
      showInNavbar: true
    },
    {
      pageLink: '/login',
      view: Login,
      displayName: 'Login',
      showInNavbar: true
    },
    {
      pageLink: '/register',
      view: Register,
      displayName: 'Register',
      showInNavbar: true
    },
    {
      pageLink: '/feed',
      view: Feed,
      displayName: 'Feed',
      showInNavbar: true
    },
    {
      pageLink: '/invite',
      view: Invite,
      displayName: 'Invite',
      showInNavbar: true
    }
  ]



 
  return (
    <div className="App">
      {!isMobile && <Header pages={pages} />}
      {isMobile && <HeaderTab />}
      <Suspense fallback={<div />} >
        <Router history={history} >
          <Switch>
            {pages.map((page, index) => {
              return (
                <Route path={page.pageLink} exact render={({ match }) => <page.view />}  key={index} />
              )
            })}
            <Redirect to="/" />
          </Switch>
        </Router>
        {isMobile && <FooterTab />}
      </Suspense>

    </div>
  );
}

export default App;
