import React from 'react'
import {Input,Button,HelpBlock} from "rsuite"
const InviteRow=(props)=> {

    const {field,data}=props
    return (
        field.map((val,idx)=>{
            let email=`Email-${idx}`
            return(
                <div className="invite-row" >
                <div key={val.idx} className="input-container" >
                    <Input placeholder="Email" name="email" data-id={idx} id={email} block   className={data!=null&&data[idx].is_error?"input-error":""}/>
                    {data!=null&&data[idx].is_error&&<HelpBlock className="helper">{data[idx].error.email}</HelpBlock>}
                </div> 
              {field[idx].index>1&&<Button appearance="ghost" block onClick={()=>props.delete(val)} color="red" >Remove</Button>}
            </div>
            )
        })
       
    )
}



export default InviteRow
